.tag-pill {
  border-radius: 15px;
  height: fit-content;
  width: fit-content;
  position: relative;
  min-height: 24px;
  white-space: nowrap;
  padding: 0px 8px;
  margin: 2px 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  cursor: pointer;
  font-size: 1em;
  // Existing styles from text-pill
  &.hate {
    // background-color: #ff6e6e67;
    // border: 2px solid #ff000067;
    color: #c74f4f;
  }
  
  &.like {
    // background-color: #86d3e867;
    // border: 2px solid #557f9267;
    color: #6acf8d;
  }
  &.conflicted {
    // background-color: #86d3e867;
    // border: 2px solid #557f9267;
    color: #7e7e7e;
  }
  &.trait {
    background-color: #d6ae8667;
    border: 2px solid #a3824f67;
  }
  
  &.branch {
    background-color: #c5d6ae67;
    border: 2px solid #889c6667;
  }
  
  &.expected {
    background-color: #aed6be67;
    border: 2px solid #82b39267;
  }
  
  &.established {
    background-color: #d6aec567;
    border: 2px solid #b39c8267;
  }
  
  &.combatant {
    background-color: #aebdd667;
    border: 2px solid #8a9cb367;
  }
  
 
  
  // Tag type styles
  &.type-location, &.location-tag {
    background-color: #695414;
    border: 4px solid #ffffff;
    color: #2c2309;
  }
  

  &.player-conflicted {
    background: linear-gradient(135deg, #86d3e867, #ff6e6e67);
    border: 2px solid #77777767;
  }
  &.type-plot {
    background-color: #000000;
    border: 2px solid #ffffff;
    color: #ffffff;
  }
  
  &.type-backstory {
    background-color: #610061;
    border: 2px solid #ffffff;
    color: #ffffff;
  }
  
  &.type-foreshadowing {
    background-color: #6bbf54;
    border: 2px solid #ffffff;
  }
  
  &.type-magic-item {
    background-color: #f2ff3fdb;
    border: 2px solid #ffffff;
  }
  
  &.type-dm {
    background-color: #ab00fa;
    border: 2px solid #ffffff;
  }
  
   &.player-like {
    color: #539a6c;
  }
  
  &.player-hate {
   color: #7b3131;
  }
  
  // States
  &.disabled, &.used {
    opacity: 0.5;
   
    filter: grayscale(50%);
  }

  &.off {
     cursor: pointer;
    pointer-events: none;
  }
  
  &.highlighted {
    transform: scale(1.05);
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
    filter: brightness(1.2);
  }
  
  &:hover:not(.disabled) {
    transform: translateY(-2px);
    filter: brightness(1.15);
    
    .tooltip {
      opacity: 1;
      transform: translateX(-50%) translateY(-8px);
    }
  }
  
  // Tooltip
  .tooltip {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%) translateY(200%);
    padding: 8px;
    background: rgba(211, 211, 211, 0.8);
    border-radius: 4px;
    font-size: 0.9em;
    opacity: 0;
    transition: all 0.3s ease;
    pointer-events: none;
    white-space: normal;
    width: auto;
    min-width: 160px;
    max-width: 300px;
    z-index: 1000;

    // Pulse animation
   // &.pulse {
      animation: tooltip-pulse 1.2s infinite;
   // }

    @keyframes tooltip-pulse {
      0% {
        box-shadow: 0 0 0 0 rgba(211, 211, 211, 0.7);
      }
      70% {
        box-shadow: 0 0 12px 8px rgba(211, 211, 211, 0.3);
      }
      100% {
        box-shadow: 0 0 0 0 rgba(211, 211, 211, 0);
      }
    }

    .tooltip-content {
      color: #363636;
      text-align: center;
    }
  
  }
}