
.dm-screen-container {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #e5ded595;
    border-radius: 10px;
    margin: 6px;
    border: 4px solid rgb(87, 77, 44);
    box-sizing: border-box;
    margin-bottom: 10px;;
    &.hidden {
        display: none;
    }
    .dm-screen-branches-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        gap: 10px;  
        width: 90%; 
        .dm-screen-branch {
            flex: 1;
        }            
    }
    .dm-screen-rumors {
       bottom: 100px;
       position: absolute;
    }
    .dm-screen-dm-level {
        font-size: 1.3em;
        margin-right: 14px;
        .fill-bar {
            font-size: .65em;
        }
    }

    .dm-screen-header {
        display: flex;
        flex-direction: row;
        height: 100px;
        align-items: center;
        width: 100%;
        margin-top:6px;
        margin-bottom: 6px;

        justify-content: space-between;
        
        .dm-screen-campaign {
            display: flex;
            width: 100%;
            height:100%;
            max-width: 50%;
            position: relative;
            align-items: center;
            .campaign-display {
                height: 80px;
                padding: 4px;
                min-width: 220px;

                .campaign-info {
                    width: 100%;
                    &.hooks{
                        font-size: .8em;
                        white-space: nowrap;
                    }
                    font-size: 1em;
                    top: 0px;
                    font-weight: bold;
                }
                .campaign-icon {
                    margin-right: 0px;
                    position: relative;
                    align-items: center;
                    display: flex;
                    justify-content: center;
                    .cimage {
                        height: 70px;
                        width: 70px;
                    }
                }
            }
            .circle-display {
                margin-top: 4px;
                
            }
        }
        .dm-screen-dm-points {
            display: flex;
            flex-direction: column;
            width: 120px;
            height: 100%;
            gap: 4px;
            box-sizing: border-box;
            margin-top: auto;
       
            .dm-screen-dm-points-label {
                    
                    font-size: 1em;
                    font-weight: bold;
                    color: #2f2a24;
                    text-align: center;
                    margin: 0px;
                    line-height: .95em;
            }
            .dm-screen-dm-point {
     
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                box-sizing: border-box;
                position: relative;
                .number-die{
                    height: 100%;
                    position: absolute;
                    width: 80px;
                    background-color: #2f2a24;
                    font-size: 1.3em;
                    border-color: #00000000;
                }
                .cimage {
                  position: relative;
                  height: 100%;
                }
               
            }
            
        }
    }
   
    .table-encounter-container {
        max-width: 99%;
        box-sizing: border-box;
        top: 0px;
        border-width: 8px;
        padding: 10px 10px;
        height: 200px;
        font-size: 1.2em;
        margin: 2px;
       
        .event-column {
            gap: 1px;
            font-size: .8em;
            padding: 0;
            .encounter-event {
                border-width: 1px;
                padding: 4px;
                text-align: center;
                justify-content: center;

                &.seen {
                    background: #ffffff33;
                }
                &.current {
                    background: white;
                    color: black;
                }

                .event-controls {
                    display: none;
                }
            }
        }
    }
  .dm-screen-collected-tags-header {
            font-size: 1.4em;
        }
    .dm-screen-collected-tags-container {
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
        margin-bottom: 15px;
        justify-content: center;
       
        .encounter-flavor-tag {
            padding: 2px 4px;
            font-size: .75em;
            position: relative;
       
        }
    }
    .dm-screen-collected-themes{
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
        padding: 10px;
    }
    .dm-screen-collected-tags-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    


    // Set the Scene
    .dm-screen-set-scene{
        display: flex;
        width: 90%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 10px;
        width: 100%;
        .fill-bar {
            width: 90%;
        }

        .dm-screen-encounter-tags-row{
            display: flex;
            flex-direction: row;
            gap: 8px;
        }
    }

    .dm-screen-set-scene-tags, .dm-screen-branch-tags {
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
        justify-content: center;
        padding: 0px;
        margin-bottom: 4px;
        .encounter-flavor-tag {
            padding: 0px 10px;
            font-size: .75em;
            margin:0px;
            position: relative;
            &.large-text{
                font-size: .9em;
                font-weight:600;
            }
        }
    }
    .fill-bar {
        margin-bottom: 4px;
        .fill-bar-container {
            margin-bottom: 4px;
            margin-top: 4px;
            
        }
    }
    

    .dm-screen-branches-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 20px;
        width: 100%;
        gap: 0px;
        .dm-screen-branches-header {
            font-size: 1.2em;
            font-weight: bold;
        }
        .dm-screen-upcoming-branches {
            display: flex;
            flex-direction: row;
            gap: 0px;
            justify-content: center;
            width: 100%;
           
            .dm-screen-branch {
                flex: 1;
                margin-left: 10px;
                margin-right: 10px;
                .fill-bar {
                    .fill-bar-label {
                        font-size: 1em;
                    }
                    .fill-bar-container {
                        margin-bottom: 4px;
                        margin-top: 4px;
                    }
                }
            }
        }
    } 
}


.campaign-score-info {
    margin: 4px;
    margin-left: 0px;
    margin-bottom: 18px;
    padding: 10px;
    border-radius: 5px;
    background-color: #aab6b6c1;
    color: rgb(84, 73, 73);
    text-shadow: 1px 1px 2px rgba(255, 255, 255, 0.5);
    border-radius: 100%;
   
    margin-top:18px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 55px;
    
    height: 55px;
    border: 4px solid #72716e;
    
    .score-value {
      position: relative;;
      font-size: 1.5em;
      top: 2px;
      font-weight: bold;
    }
    .score-label {
        position: relative;
        top: 9px;
        
        font-size: .7em;
        font-weight: bold;
      }
    &.overview {
       width: 96px;
       max-width: 96px;
       height: 76px;
       .score-value {
            font-size: 2em;
            top: 1px;
       }
         .score-label {
              font-size: .8em;
              top: 10px;
         }
    }
   
}