.slot-machine-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000000; /* Fully opaque black */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.slot-machine-modal-content {
 
  max-height: 90vh;
  
  animation: slideIn 0.3s;
  padding: 0; /* Remove padding to let the casino theme shine */
  border-radius: 10px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.8);
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.col-labels{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 10px;
}

.casino-sign {
  text-align: center;
  margin-bottom: 30px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  border: 4px solid #2c0030;
  box-shadow: 
    0 0 20px rgba(255, 0, 255, 0.3),
    inset 0 0 20px rgba(255, 0, 255, 0.2);
  animation: borderGlow 2s infinite alternate;

  h1 {
    font-size: 48px;
    font-family: 'Arial Black', sans-serif;
    margin: 0;
    color: #ff69b4;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-shadow: 
      0 0 7px #ff69b4,
      0 0 10px #ff69b4,
      0 0 21px #ff69b4,
      0 0 42px #ff1493,
      0 0 82px #ff1493,
      0 0 92px #ff1493;
    animation: textGlow 1.5s infinite alternate;
  }

  .sign-subtitle {
    font-size: 20px;
    color: #00ffff;
    margin-top: 10px;
    font-weight: bold;
    letter-spacing: 6px;
    text-shadow: 
      0 0 7px #00ffff,
      0 0 10px #00ffff,
      0 0 21px #00ffff,
      0 0 42px #00bfff;
    animation: subtitleGlow 2s infinite alternate;
  }
}

@keyframes borderGlow {
  from {
    box-shadow: 
      0 0 20px rgba(255, 0, 255, 0.3),
      inset 0 0 20px rgba(255, 0, 255, 0.2);
  }
  to {
    box-shadow: 
      0 0 30px rgba(255, 0, 255, 0.5),
      inset 0 0 30px rgba(255, 0, 255, 0.4);
  }
}

@keyframes textGlow {
  from {
    text-shadow: 
      0 0 7px #ff69b4,
      0 0 10px #ff69b4,
      0 0 21px #ff69b4,
      0 0 42px #ff1493;
  }
  to {
    text-shadow: 
      0 0 7px #ff69b4,
      0 0 10px #ff69b4,
      0 0 21px #ff69b4,
      0 0 42px #ff1493,
      0 0 82px #ff1493,
      0 0 92px #ff1493;
  }
}

@keyframes subtitleGlow {
  from {
    text-shadow: 
      0 0 7px #00ffff,
      0 0 10px #00ffff;
  }
  to {
    text-shadow: 
      0 0 7px #00ffff,
      0 0 10px #00ffff,
      0 0 21px #00ffff,
      0 0 42px #00bfff;
  }
}