.modal-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.7);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	backdrop-filter: blur(4px);
}

.modal-container {
	background: #2a2a3a;
	border-radius: 24px;
	padding: 24px;
	// width: 90%;
	border: 4px solid #4a4a5a;
	box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
	animation: modalAppear 0.3s ease-out;
	&.travel {
		width: 50%;
		max-width: 800px;
	}
}

.modal-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 24px;
	padding-bottom: 16px;
	border-bottom: 2px solid #4a4a5a;

	h2 {
		color: #ffffff;
		font-size: 24px;
		margin: 0;
	}

	.close-button {
		background: none;
		border: none;
		color: #ffffff;
		font-size: 24px;
		cursor: pointer;
		padding: 8px;
		border-radius: 50%;
		transition: background-color 0.2s;

		&:hover {
			background-color: #ffffff20;
		}
	}
}

.modal-options {
	display: flex;
	flex-direction: row;
	gap: 20px;
	justify-content: center;
	max-height: 70vh;
	overflow-y: auto;
	padding: 8px;

	.modal-option {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 24px 16px;
		background: #3a3a4a;
		border: 6px solid #5a5a6a;
		border-radius: 16px;
		cursor: pointer;
		transition: all 0.2s ease;
		overflow: hidden;
		max-width: 350px;
		width: 350px;
		&:hover {
			transform: translateY(-4px);
			border-color: #7a7a8a;
			background: #4a4a5a;

			.option-icon {
				transform: scale(1.1);
			}
		}

		&:active {
			transform: translateY(0);
		}

		&.level-up-option-character {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #8a8a9a;
			.cimage {
				position: relative;
				width: 80px;
				height: 80px;
				border-radius: 100%;
				margin-bottom: 10px;
			}
		}
		&.rare {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #6d77ff;
		}
		&.legend {
			background: linear-gradient(135deg, #4a4a5a, #6a6a7a);
			border-color: #ffb732;
		}

		.option-icon {
			font-size: 48px;
			margin: 16px 0;
			transition: transform 0.2s ease;
		}

		.option-content {
			text-align: center;
			width: 100%;

			h3 {
				color: #ffffff;
				margin: 0 0 8px 0;
				font-size: 18px;
			}

			p {
				color: #cccccc;
				margin: 0;
				font-size: 14px;
				line-height: 1.4;
			}
		}
	}
}

@keyframes modalAppear {
	from {
		opacity: 0;
		transform: translateY(-20px);
	}
	to {
		opacity: 1;
		transform: translateY(0);
	}
}

// Custom scrollbar for modal options
.options {
	&::-webkit-scrollbar {
		width: 8px;
	}

	&::-webkit-scrollbar-track {
		background: #2a2a3a;
		border-radius: 4px;
	}

	&::-webkit-scrollbar-thumb {
		background: #5a5a6a;
		border-radius: 4px;

		&:hover {
			background: #6a6a7a;
		}
	}
}


