// -------------------------------------------------------------------------------------------------------------------
// GROUP
// -------------------------------------------------------------------------------------------------------------------
.player-recruitment-mode {
  .notebook-modal {
    .notebook-content {
      height: 50%;
      top: 100px;
    }
  }
}

.notebook-group-container {
  font-size: large;
  align-items: center; // Center vertically

  .notebook-group-players {
    top: 13px;
    .notebook-group-players-list {
      display: flex;
      flex-wrap: wrap; // Allow items to wrap to the next line if needed
      margin-top: 10px;
      justify-content: center;
    }

    .player-name {
      display: inline-block; // Ensure each player name behaves like an inline element

      border-radius: 15px; // Optional: Add border radius for pill shape
      line-height: 2em;
    }
  }

  .notebook-group-top-content {
    display: flex;

    justify-content: center;
    position: relative;
  }

  .player-attitude {
    .attitude-column {
      margin-top: 39px;
      display: flex;

      align-items: center; // Center vertically
      flex-direction: column; //
    }
  }
  .player-attitude-conflicted {
    align-items: center; // Center vertically
    display: flex;
    .attitude-item {
      top: 4px;
    }
  }
  .attitude-item {
    position: relative;
    margin-bottom: -9px;
    height: 10px;
    top: -6px;
    width: fit-content;
    font-weight: bold;
    .attitude-item-text {
      top: -8px;
      position: relative;
      text-align: center;
    }
  }
}

// -------------------------------------------------------------------------------------------------------------------
// Character
// -------------------------------------------------------------------------------------------------------------------

.notebook-character {
  width: 100%;
  display: flex;
  flex-direction: row;
 
  .notebook-character-left,
  .notebook-character-right {
    flex: 1; /* Each will take up 50% of the space */
    padding: 10px; /* Optional: Add padding if needed */
    box-sizing: border-box; /* Ensure padding and border are included in the element's total width and height */
    max-width: 50%;
  }
  .notebook-text {
    line-height: 31px;
  }

  .header-text {
    font-size: var(--notebook-header-font-size-small);
  }

  .attribute-display {
    font-size: 20px;
  }

  .notebook-character-left {
    .notebook-left-top-container {
      display: flex;
      flex-direction: row;
      
      .notebook-character-icon-container {
        width: 100%;
            height: 90px;  
        .cimage {
          width: 100%;
          border-radius: 100%;
        }
      }

      .notebook-character-stats {
        width: calc(100% - 100px);
        margin-left: 10px;
        text-align: left;
        padding-left: 10px;
        font-size: .8em;
        line-height: 1.1em;
      }
     
    }

    .notebook-backstory-tags {
        margin-bottom: 6px;
      }
   
  }

  .notebook-character-right {
   // width: 50%;
    line-height: 31px;
    margin-right: 12px;
    .notebook-right-container {
      display: flex;
      flex-direction: column;
      position: relative;
      top: -4px;
      .notebook-character-ability-breakdown-container {
        margin-left: 10px;
        .notebook-character-race-abilities {
          display: flex;
          flex-direction: row;
        }
        .notebook-character-ability-breakdown {
          top: -2px;
          position: relative;
        }
        .notebook-character-ability-levels {
          margin-left: 40px;
        }
        .gear-attributes {
          display: flex;
          flex-direction: row;

          margin-left: 40px;
        }
        .level-attributes {
          display: flex;
          flex-direction: row;
          gap: 0px;
        }

        .attribute-display-container {
            
          .collapsed-attributes {
            margin-left: 12px;
          }
        }
      }
    }
   

  }
  .attribute-display-container {
    
    .attribute-icon {
      border-radius: 100%;
      height: 30px;
      aspect-ratio: 1 / 1; /* Maintain a 1:1 aspect ratio */
      background-color: #8a8a8a;
      display: inline-block; /* Ensure the element respects the aspect ratio */
      background-position: center; /* Center the background */
      text-align: center;
      margin-right: 2px;
      margin-bottom: 1px;
      &.combat {
        background-color: #c27039;
      }
      &.magic {
        background-color: #9d49a7;
      }
    }
  }
  .cbox { 
    height: unset;
   .cbox {
    margin: 0px;
   }
    &.character-info {
      width: 100%;
      border-color: var(--notebook-bg-color-character);
      .cbox-text {
        background-color: var(--notebook-bg-color-character);
        font-size: 1em;
        font-weight: bold;
        color: black;
      }
      .cbox-children {
        padding: 10px;
        gap: 29px;
        font-size: .65em;
        .notebook-character-icon-container {
          width: 115px;
          height: 115px;
          min-width: 115px;
          .cimage {
            width: 100%;
            height: 100%;
            border-radius: 100%;
          }
        }
     
      }
    }

    &.health {

      border-color: var(--notebook-bg-color-character-health);
      margin-top: 8px;
      max-width: 200px;
      background-color: white;
    
      .cbox-text {
        background-color: var(--notebook-bg-color-character-health);
        font-size: .25em;
        font-weight: bold;
        color: black;
      }
      &.injured {
        border-color:  #d07931;
        .cbox-text {
            background-color: #d07931; 
        }
      }
      &.dead {
        border-color:  #c43030;
        .cbox-text {
            background-color: #c43030; 
        }
      }
      .health-status {
        font-size: .5em;
        text-align: center;
        font-weight: bold;
        padding: 0px;
        &.injured {
             color: #d07931;
            }
        &.dead { 
            color: #933838; 
        }
      }
    }

    &.abilities {
      border-color: var(--notebook-bg-color-character-abilities);
      margin-top: 8px;
      font-size: 1em;
      line-height: 1.8em;
      background-color:white;
      .cbox-children{
        gap: 20px;
        
      }
      .cbox-text {
        background-color: var(--notebook-bg-color-character-abilities);
        font-size: 2em;
        font-weight: bold;
        color: black;
        padding-bottom: 10px;
      }
     .attribute-title {
        font-size: 1em;
        margin-top: -2px;
     }
    }

    &.backstory {
      border-color: var(--notebook-bg-color-character-backstory);
      margin-top: 8px;
      font-size: 1em;
     
  
      .cbox-text {
        background-color: var(--notebook-bg-color-character-backstory);
        font-size: 2em;
        font-weight: bold;
        color: black;
      }
      .cbox-children {
        font-size: 1em;
        align-items: center;
        height: 100%;
        background-color: white;
     
        
        .text-pill {
          margin: 4px;
          &.expected {
            background-color: white;
            color: var(--info-display-pen);
            border: 2px solid var(--info-display-pen);
          }
          &.completed {
            background-color: var(--info-display-pencil);
            color: white;
            border: 2px solid var(--info-display-pen);
          }
        }
      }
    }

    &.ability-details {
      border-color: var(--notebook-bg-color-character-ability-details);
      font-size: 1em;
      .cbox-text {
        background-color: var(--notebook-bg-color-character-ability-details);
        font-size: 2em;
        font-weight: bold;
        color: black;
        padding-bottom: 4px;
      }
      .cbox-children {
        background-color: var(--notebook-bg-color-character-ability-details-light);
        padding: 8px;
        gap: 8px;
        .cbox {
            
            font-size: 1em;
            .cbox-children{
                background-color: white;
                border-bottom-left-radius: 10px;;
                border-bottom-right-radius: 10px;;
            }
        }
      }
      
    }

    &.race-abilities {
        border-color: var(--notebook-bg-color-character-ability-details-race);
        font-size: 1em;
        .cbox-text {
          background-color: var(--notebook-bg-color-character-ability-details-race);
          font-size: 1.5em;
          font-weight: bold;
          color: black;
        }
        .cbox-children{
            padding: 6px;
        }
    }

    &.class-abilities {
        border-color: var(--notebook-bg-color-character-ability-details-class);
        
        .cbox-text {
          background-color: var(--notebook-bg-color-character-ability-details-class);
          font-size: 1.5em;
          font-weight: bold;
          color: black;
        }
        .cbox-children{
            padding: 6px;
            padding-top: 0px;
        }
    }
  }
}

// -------------------------------------------------------------------------------------------------------------------
// Single-column
// -------------------------------------------------------------------------------------------------------------------

.notebook-modal.single-column {
  .title-text {
    display: none;
    &.lower {
      display: inline-block;
      top: 0px;
      position: relative;
      font-size: 2.1em;
      font-weight: bold;
    }
  }
  .notebook-player-container {
    position: relative;
    top: 6px;
    flex-direction: column;
  }
  .notebook-header {
    height: 5.2em;
    .notebook-header-title {
      top: 50%;
    }
  }

  .notebook-content {
    background-image: linear-gradient(
      to bottom,
      var(--notebook-bg-color) 20px,
      #a89e9e 21px
    );
    background-size: 100% 21px;
    display: flex;
    flex-direction: column;
  }

  .notebook-text {
    line-height: 21px;
  }
  .notebook-tabs {
    top: -3.1em;
  }
  .notebook-tab {
    &.current {
      top: 4px;
      position: relative;
    }
  }

  .player-preferences-container {
    left: 5px;
    top: 20px;
    .player-preference-title-small {
      display: inline-block;
      left: 10px;
      position: relative;
      margin-left: -10px;
      padding-left: 20px;
      font-size: 1.1em;
      font-weight: bold;
    }
    .title-text {
      top: 0px;
      display: none;
    }
    .player-preferences {
      .text-pill {
        margin-top: -0.1em;
        height: 0.9em;
        margin-bottom: 0.2em;
        .notebook-text {
          top: -0.2em;
        }
      }
      .trait-description {
        line-height: 19px;
      }
    }
  }
  .player-notebook-right-content {
    max-width: 100%;
  }

  .player-schedule-container {
    left: 10%;
    width: 80%;
    bottom: 20%;
    margin-top: 22px;
  }
}
