.main-menu-container {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
}

.main-menu-content {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	color: #13161e;
	text-align: center;
	.game-start-container {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 20px;
		left: 20px;
		gap: 10px;
	}

	.button-container {
		margin: 5px;
	}

}

.main-menu-title {
	font-size: 2.5em;
	margin-bottom: 20px;
	color: white;
}

.main-menu-options {
	display: flex;
	flex-direction: column;
	gap: 10px;
	margin-bottom: 20px;
	align-items: center;
	top: 28%;
	position: absolute;
	.nav-bar-container {
		display: grid;
		grid-template-columns: 1fr 1fr; // Two columns
		grid-template-rows: auto auto; // Two rows
		gap: 10px; // Gap between grid items
		justify-items: center; // Center items horizontally
		align-items: center; // Center items vertically
	  
		.nav-button:nth-child(1) {
		  grid-column: 1; // First item in the first column
		  grid-row: 1; // First item in the first row
		}
	  
		.nav-button:nth-child(2) {
		  grid-column: 2; // Second item in the second column
		  grid-row: 1; // Second item in the first row
		}
	  
		.nav-button:nth-child(3) {
		  grid-column: 1 / span 2; // Third item spans both columns
		  grid-row: 2; // Third item in the second row
		  justify-self: center; // Center the third item horizontally
		  top: -40px;
		  position: relative;
		}
	  }
}


.exit-button:hover {
	background-color: rgba(255, 0, 0, 0.9);
}

.minigame-button {
	background-color: #8e44ad;
	color: white;
	padding: 10px 20px;
	font-size: 1.2em;
	border-radius: 8px;
	cursor: pointer;
	transition: all 0.2s;
	box-shadow: 0 4px 0 #6c3483;
	
	&:hover {
		background-color: #9b59b6;
		transform: translateY(2px);
		box-shadow: 0 2px 0 #6c3483;
	}
	
	&:active {
		transform: translateY(4px);
		box-shadow: none;
	}
}